
/* To prevent Navbar from changing positions when page does not have scroll bar. */
html
{
  overflow-y: scroll;
  background: #f3f3f3;
}
.App {
  text-align: left;
  font-family: "Oswald", sans-serif;
  /* font-family: 'Poppins', sans-serif; */
  font-size: 16px;


}
.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 110px;
  background: white;
  color: black;
  box-shadow: 0px 8px 8px -6px rgba(0, 0, 0, 0.5);
  margin-bottom: 0;
  width: 100%;
}

footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: black;
  color: white;
}

h1 {
  font-family: "Oswald", sans-serif;
  font-weight: bold;
  font-size: 2.5rem;
  color: #faa919;
}

h2 {
  font-family: "Oswald", sans-serif;
  font-size: 1.5rem;
  line-height: 120%;
  color: #009A7B;
}

h3 {
  font-size: 1.2rem;
}

button {
  color: black;
  font-size: 1rem;
  padding: 0.8rem 1rem;
  background-color: transparent;
  border: none;
}

button:hover {
  background-color: black;
  color: white;
}

.logo {
  height: 100px;
  width: auto;
  padding: 1rem;
}

p {
  margin: auto;
  margin-bottom: 18px;
  font-weight: 10;
  font-size: 12pt;
  color: #ffb83c;
}

ul {
  list-style: none;
  text-align: left;
  padding: 0;
}

.top {
  background-color: #009A7B;
  height: 70px;
}

.nav-links {
  width: 35%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.1rem;
  font-weight: 350;
}

.link {
  color: white;
  text-decoration: none;
}

.nav-link {
  text-decoration: none;
  color: black;
}

.nav-link:hover {
  color: #faa919;
}

.home-container {
  background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3) ), url("./images/home-main.png");
  background-size: cover;
  min-height: 600px;
  background-position: center center;
}

.home-image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  background-image: url("./images/bench.jpg");
  background-position: 0% 0%;
  background-attachment: fixed;
  background-size: cover;
  height: 550px;
}

.donate-btn {
  background-color: #009A7B;
  border: solid #009A7B 2px;
  color: #faa919;;
  padding: 0.2rem 1.4rem;
  text-decoration: none;
  letter-spacing: 1.2px;
  font-weight: bold;
  text-transform: uppercase;
}

.donate-btn:hover {
  background-color: transparent;
  color: #009A7B;
  text-decoration: none;
}

.section {
  padding: 120px;
  display: flex;
  text-align: left;
  flex-wrap: wrap;
}

.info {
  flex: 1;
  margin: auto;
  padding: 50px;
  padding-top: 0;
}

.image {
  object-fit: cover;
  max-width: 50%;
  margin: auto;
}

.reverse {
  flex-direction: row-reverse;
}

@media (max-width: 667px) {
  .image {
    max-width: 100%;
    margin: 0;
  }
}

.column-container {
  display: inline-flex;
  gap: 10%;
}

.column-container > * {
  flex: 1;
}

.column-img,
.column-h,
.column-p {
  width: 100%;
  text-align: left;
}

.column-h {
  margin-bottom: 8%;
  margin-top: 8%;
}

.donate-img {
  width: 200px;
}

.services-main {
  width: 80%;
  height: auto;
  margin-bottom: 5%;
}

.services-img {
  object-fit: contain;
}

.home-page-top {
  margin-left: 12%;
  margin-right: 12%;
}

.line-header {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  margin-top: 4%;
}

.line-header:before,
.line-header:after {
  content: "";
  border-top: 2px solid;
  flex: 0.26 0 20px;
}

.instagram {
  width: 100px;
}

.facebook {
  padding: 30px;
  width: 105px;
}

.icon {
  padding: 20px;
  width: 90px;
}

.social-media-icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flyer {
  background-color: rgb(255, 236, 208);
}

.welcome {
  background-color: #faa919;
  padding: 2%;
  padding-left: 5%;
  padding-right: 5%;
  margin-right: 45%;
  text-align: left;
}

.welcome > h3 {
  color: #009A7B;
  font-weight: 500;
  margin-bottom: 1%;
}

.welcome > p {
  color: rgb(255, 236, 208);
  padding-right: 12%;
}

.home-data {
  background-color: rgb(255, 236, 208);
  display: flex;
  padding-left: 4%;
  padding-right: 4%;
}

.calendar-container {
  margin: 5%;
  margin-left: 20%;
  margin-right: 20%;
}

.donate {
  text-align: left;
  line-height: 25pt;
}

.donate p {
  color: black;
}

.donate h3 {
  color: #faa919;
}

.donate h3, .donate h4 {
  line-height: 22pt;
}

.donations-container {
  display: inline-flex;
  margin: 1.5%;
  gap: 15%;
}

.donations-container > * {
  flex: 1;
}

.donate-row, .donate-header, .appr-of-funding > * {
  margin-left: 10% !important;
  margin-right: 10% !important;
}

.donate-header > * {
  margin-bottom: 2%;
}

.donate-header > h2 {
  margin-top: 5%;
}

hr {
  border: none;
  height: 1px;
  background-color: black;
}

.donate-links {
  display: flex;
  flex-wrap: wrap;
}

.donate-links img {
  height: 30px;
}

.donate-links p {
  margin-left: 8%;
}

.donate-links a {
  display: flex;
  margin-right: 5%;
}

.affiliate-container {
  display: flex;
  margin-bottom: 1%;
}

.affiliate-img {
  width: 100%;
  margin-top: 10%;
  margin-left: auto;
  margin-bottom: auto;
}

.affiliate-container > div > div {
  margin-bottom: 4%;
}

.appr-of-funding {
  margin: 0;
  background-color: #e9e9e9;
}

.appr-of-funding h2 {
  padding-top: 5%;
}

.funding-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 1%;
}

.funding-container h4 {
  font-size: 16pt;
}

.funding-container > * {
  margin-bottom: 1%;
}

@media screen and (min-width: 900px) {
  .funding-container > * {
    max-width: 29%;
  }
}

@media screen and (max-width: 900px) {
  .funding-container > * {
    max-width: 46%;
  }
}

@media screen and (max-width: 600px) {
  .funding-container > * {
    max-width: 99%;
  }
}

.btm-pd {
  padding-bottom: 5%;
}

.col-md, .col-md-6, .col-lg-4 {
  padding: 0;
}

.slider-img {
  list-style: none;
  max-height: 100%;
  max-width: 90%;
}

.swiper-container {
  margin: auto;
  width: 100%;
  height: 700px;
  margin-bottom: 2%;
}

@media screen and (min-width: 800px) {
  .swiper-container {
    width: 768px;
  }
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-wrapper {
  padding-inline-start: 0;
}

.swiper-button-prev {
  color: #009A7B !important;
}

.swiper-button-next {
  color: #009A7B !important;
}

.swiper-pagination-bullet-active {
  background-color: #009A7B !important;
}

.swiper-pagination {
  position: absolute;
  bottom: 30px !important;
}




/* Row -bootsrap remove Margin */

.row
{
  margin: 0;
}
